.search-form__nav {
  border-top: 1px solid $colorGrey;
}
.search-form__nav-item {
  display: inline-block;
  padding: 8px 48px 8px 6px;
}
.search-form__nav-item,
.search-form__nav-option {
  color: $colorDarkGreen;
  cursor: pointer;
}
.search-form__nav-option {
  white-space: nowrap;
  line-height: 33px;
}
.search-form__nav-item:hover {
  .search-form__nav__content {
    display: block;
  }
}
.search-form__nav__content {
  display: none;
  border-radius: 0 0 5px 5px;
  border-top: 1px solid $colorGrey;
  position: absolute;
  top: 34px;
  left: 0;
  padding: 9px 57px 10px 15px;
  background-color: $white;
}