.fast-buy__range {
  padding-top: 17px;
  // margin-left: 13px;
}
.fast-buy__range__minus,
.fast-buy__range__plus {
  cursor: pointer;
}
.fast-buy__range__minus{
  @include sprite(minus);
}
.fast-buy__range__plus{
  @include sprite(plus);
}
.fast-buy__range__val{
  width: 48px;
}

.fast-buy__range__amount {
  margin-bottom: 25px;
}
.fast-buy__range__step {
  margin-top: 5px;
  text-align: justify;
  text-align-last: justify;
  font-size: 12px;
  color: $colorGrey;
}