@import '__input/search-form__input.scss';
@import '__nav/search-form__nav.scss';
@import '__close/search-form__close.scss';
.search-form {
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 10;

  &__content {
    background-color: $white;
    left: 0;
    position: relative;
    right: 0;
    top: -70px;
    z-index: 10;
  }
}
.search-form__link__icon {
  margin-top: -15px;
  &:before {
    padding-right: 7px;
    vertical-align: bottom;
  }
}
.search-form__icon {
  &:before {
    content: '';
    display: inline-block;
    @include sprite(Search);
    font-size: 16px;
  }
}
.search-form__wrap {
  vertical-align: middle;
  padding: 7px 0 4px;
}
.search-form__wrap__icon {
  margin: 4px 0 0 5px;
}