.slider {
  height: 577px;
  left: 0;
  list-style-type: none;
  margin-top: -50px;
  overflow: hidden;
  padding: 0px;
  position: relative;
  top: 0;
  width: 100%;
  z-index: 1;
  &__item {
    -moz-transition: opacity .6s ease-in-out;
    -o-transition: opacity .6s ease-in-out;
    -webkit-transition: opacity .6s ease-in-out;
    height: 100%;
    left: 0px;
    opacity: 0;
    position: absolute;
    top: 0px;
    transition: opacity .6s ease-in-out;
    width: 100%;
    z-index: 1;
    &_show {
      opacity: 1;
      z-index: 2;
    }
    &__img {
      display: block;
      left: 0;
      max-width: 100%;
      min-height: 100%;
      position: absolute;
      top: 0;
    }
    &__txt {
      left: -135px;
      z-index: 3;
      position: absolute;
      top: 72px;
      width: 100%;
    }
  }
}
.bullet-wrap {
  position: relative;
}

.bullet {
  bottom: 30px;
  left: -133px;
  margin: 0 -10px;
  position: absolute;
  width: 100%;
  z-index: 3;
  &__item {
    background-color: $white;
    border-radius: 50%;
    cursor: pointer;
    height: 14px;
    width: 14px;
    margin: 0 10px;
    &_active {
      background-color: $booletColor;
    }
  }
}