.header__cart {
  display: block;
  color: #fff;
  margin: 3px 0 7px;
  position: relative;
}
.header__cart-val {
  border-radius: 50%;
  width: 14px;
  height: 14px;
  background-color: $colorPinkLight;
  font-family: "sfL";
  font-size: 10px;
  line-height: 14px;
  text-align: center;
  position: absolute;
  left: 20px;
  top: -6px;
}
.header__cart__icon {
  position: relative;
  padding-left: 42px;
  vertical-align: bottom;
  &:before {
    content: '';
    @include sprite(Cart);
    position: absolute;
    left: 0;
    font-size: 17px;
    top: -2px;
  }
}